.checkbox-thumb {
  width: 50px;
  height: 16px;
  border: 1px solid red;
  border-radius: 15px;
  position: relative;
  overflow: hidden;
}

.checkbox {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

.theme-toggle {
  width: 100%;
  height: 100%;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
}

.checkbox-circle {
  width: 10px;
  height: 10px;
  background-color: orange;
  border-radius: 50%;
  transition: all .35s cubic-bezier(.445, .05, .55, .95);
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translate(0, -50%);
}

.checkbox:checked ~ .checkbox-circle {
  transform: translate(30px, -50%);
}

body.light {
  background-color: #0ff;
}

body.dark {
  background-color: gray;
}

body.light .title, body.light .text {
  color: #fff;
  background-color: red;
}

body.dark .title, body.dark .text {
  color: #000;
  background-color: #fff;
}

